'use client'

import Link from "next/link"
import { Button } from "../ui/Button/Button"
import { FormInput } from "../ui/FormInput/FormInput"

function RegisterForm() {
    return (
        <form name="registerForm" className="flex flex-col gap-[20px] items-center justify-center border-1 border-sky-300 pt-15 pb-15 pl-10 pr-10 rounded-[20px] shadow-md max-w-fit">
            <h2 className="font-medium text-lg m-0">Регистрация</h2>
            <div className="flex flex-col gap-[12px]">
                <FormInput placeholder="Имя" type="text" label="Имя" />
                <FormInput placeholder="Фамилия" type="text" label="Фамилия" />
                <FormInput placeholder="E-mail" type="email" label="E-mail" />
                <FormInput placeholder="Пароль" type="password" label="Пароль" />
                <FormInput placeholder="Подтвердить пароль" type="password" label="Пароль" />
            </div>
            <Button classname={"font-medium bg-blue-500 w-50 h-10 rounded flex justify-center items-center text-white cursor-pointer hover:bg-blue-700 hover:shadow-lg transition duration-300 ease-in-out"} type="submit">
                Зарегистрироваться
            </Button>
            <Link href={'auth'}>
                <Button classname="cursor-pointer">
                    Войти
                </Button>
            </Link>
        </form>
    )
}

export default RegisterForm